@import url('https://fonts.googleapis.com/css2?family=Duru+Sans&family=Lato&display=swap');

body {
  margin: 0;
  font-family: 'Duru Sans', sans-serif;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
